// IMPORT FONTOW
@import url('https://fonts.googleapis.com/css?family=Lato:400,700,900');
@import url('https://fonts.googleapis.com/css?family=Exo+2:800');


$primary-color: #fff;
$shadow-color:rgba(0, 0, 0, 0.5);

body{
  background: url('../img/background.jpg') no-repeat center center fixed;
  background-size: cover;
  color: $primary-color;

}
.container-fluid{
  display: flex;
  justify-content: center;
  align-content: center;
  .register-form{
    // background-color: rgba(202, 202, 202, 0.82);
    padding: 0;
    width: 50%;
    margin: auto;
    box-shadow: 0px 0px 20px 0px $shadow-color;
    // do usuniecia
    margin-top: 20px;
    .blurred{
      position: absolute;
      top:0;
      left:0;
      background-color: rgba(202, 202, 202, 0.82);
      bottom: 0;
      right: 0;
      z-index: -1;
      filter: blur(8px);
      background: url('../img/background.jpg') no-repeat center center fixed;
      background-size: cover;
    }

    // filter: blur(5px);
    .main-title{
      font-family: 'Exo 2', sans-serif;
      text-transform: uppercase;
      text-align: center;
      font-weight: 800;
      font-size: 50px;
      margin: 25px auto 29px auto;
    }
    ul{
      padding: 0;
      display: flex;
      flex-direction: row;
      justify-content: space-around;
      flex-wrap: wrap;
      li{
        list-style: none;
        img{
          display: block;
        }
      }
    }
    hr{
      margin-top: 15px;
    }
    .box{
      width: 50%;
      padding: 0;
      position: relative;
      float: left;
    }
    .bold{
      font-weight: 700;
      font-size: 21px
    }
    p{
      font-family: 'Lato', sans-serif;
      font-weight: 400;
      font-size: 16px;
      padding: 15px 0 5px 30px;
    }

    input{
      width: 90%;
      border: none;
      border-bottom: 1px solid #fff;
      margin: 32px 0 25px 10px;
      padding: 0 15px;
      background-color: transparent;
      box-sizing: border-box;
      &:focus {
        border: none;
      }
    }
    button{
      font-family: 'Lato', sans-serif;
      width: 100%;
      background-color: $primary-color;
      color: #000;
      text-transform: uppercase;
      padding: 10px 0;
      font-weight: 900;
      font-size: 21px;
      border: 0;
    }
  }
}
